import { useCallback, useState } from 'react';
import { fabric } from 'fabric';
import { Canvas, IEvent, Object } from 'fabric/fabric-impl';
import CurevedLine from '../fabric/curvedLine';

interface Point {
  x: number;
  y: number;
}

export type Tool = 'circle' | 'rect' | 'text' | 'line' | 'select';

function useFabricCanvas(width: number, height: number) {
  const [canvas, setCanvas] = useState<Canvas>();
  const [tool, setTool] = useState<Tool>('select');
  const [currentSelected, setCurrentSelected] = useState<Object>();

  const init = useCallback(
    (canvas: HTMLCanvasElement) => {
      setCanvas(new fabric.Canvas(canvas, { width, height }));
    },
    [height, width]
  );

  function renderAll() {
    canvas?.renderAll();
  }

  function changeCurrentSelected(item: Object) {
    setCurrentSelected(item);
  }

  function changeTool(tool: Tool) {
    setTool(tool);
    renderAll();
  }

  function addRect(point?: Point) {
    if (!canvas) return;
    const rect = new fabric.Rect({
      originX: 'center',
      originY: 'center',
      width: 50,
      height: 50,
      stroke: 'black',
      fill: undefined,
      left: point?.x,
      top: point?.y,
    });
    rect.set('type', 'rect');
    canvas.add(rect);
  }

  function addLine(point?: Point) {
    if (!canvas) return;
    const line = new CurevedLine(
      { x: 0, y: height / 2 },
      { x: 630, y: height / 2 },
      { left: point?.x, top: point?.y, originX: 'center', originY: 'center' }
    );
    line.set('type', 'line');
    canvas.add(line);
  }

  function addCircle(point?: Point) {
    if (!canvas) return;
    const circle = new fabric.Circle({
      originX: 'center',
      originY: 'center',
      radius: 50,
      stroke: 'black',
      fill: undefined,
      left: point?.x,
      top: point?.y,
    });
    circle.set('type', 'circle');
    canvas.add(circle);
  }

  function addText(point?: Point) {
    if (!canvas) return;
    const text = new fabric.IText('텍스트를 입력하세요.', {
      editable: true,
      originX: 'center',
      originY: 'center',
      left: point?.x,
      top: point?.y,
    });
    text.set('type', 'text');
    canvas.add(text);
  }

  function onMouseDown(callback: (options: IEvent) => void) {
    if (!canvas) return;
    canvas.on('mouse:down', (options) => {
      callback(options);
      canvas.off('mouse:down');
    });
  }

  function onObjctAdded(callback: (options: IEvent) => void) {
    if (!canvas) return;
    canvas.on('object:added', (options) => {
      callback(options);
      canvas.off('object:added');
    });
  }

  function onObjctSelected(callback: (options: IEvent) => void) {
    if (!canvas) return;
    canvas.on('object:selected', (options) => {
      callback(options);
      canvas.off('object:selected');
    });
  }

  function removeListeners() {
    if (!canvas) return;
    canvas.removeListeners();
  }

  function offObjectAdded() {
    if (!canvas) return;
    canvas.off('object:added');
  }

  function drawItem(point?: Point) {
    switch (tool) {
      case 'circle':
        addCircle(point);
        break;
      case 'rect':
        addRect(point);
        break;
      case 'text':
        addText(point);
        break;
      case 'line':
        addLine(point);
        break;
      case 'select':
        break;
      default:
        throw new Error(`invalid tool: ${tool}`);
    }
  }

  return {
    canvas,
    tool,
    currentSelected,
    init,
    renderAll,
    drawItem,
    changeTool,
    changeCurrentSelected,
    onMouseDown,
    onObjctAdded,
    onObjctSelected,
    removeListeners,
    offObjectAdded,
  };
}

export default useFabricCanvas;
